*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

 a, a:hover, a:focus, a:active {
      text-decoration: none;
      color: inherit;
 }

.hero-bkg{
  background: gray url(https://www.toptal.com/designers/subtlepatterns/patterns/moroccan-flower-dark.png) repeat 0 0;
  width: 100%;
  margin: 0;
  text-align: center;
  height: 300px;
  padding-top: 120px;
  box-sizing: border-box;
  animation: slide 20s linear infinite;
}

@keyframes slide {
    from { background-position: 0 0; }
    to { background-position: -400px 0; }
}


#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

/*typography*/
h1,h2,h3,h4,h5,h6,p {
  font-family: 'Lato', sans-serif;
}

h1 {
  font-size: 48.83px;
  line-height: 53.96px;
}

h2 {
  font-size: 39.06px;
  line-height: 38.67px;
}

h3 {
  font-size: 31.25px;
  line-height: 34.53px;
}

h4 {
  font-size: 25px;
  line-height: 31.37px;
}

h5 {
  font-size: 20px;
  line-height: 32.9px;
}

p {
  font-size: 16px;
  line-height: 23.2px;
}

.caption {
  font-size: 12.8px;
  line-height: 17.54px;
}

.minicaption {
  font-size: 10.24px;
  line-height: 15.82px;
}